import { z } from "zod"
import { apiKeysQuotasSchema } from "@rp/common/zod-contracts/api-keys"
import { generateApiKeysQuotas } from "../../../database/generators"
import { bigdataApiProcedure } from "../bigdata-api-procedure"

const apiKeysQuotasProcedures = {
  get: bigdataApiProcedure
    .meta({ openapi: { method: "GET", path: "/keys-quotas" } })
    .input(z.object({}))
    .output(apiKeysQuotasSchema)
    .query(() => {
      return generateApiKeysQuotas()
    }),
}

export { apiKeysQuotasProcedures }
